.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: opacity .3s linear, height 0.5s linear;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .navegacion a {
    color: #1b1a1a;
    font-size: 2.4rem;
    font-weight: 700;
    transition: all 250ms ease; }
    .navegacion a:hover {
      transform: scale(1.2);
      color: #2239D4; }
  @media (min-width: 768px) {
    .navegacion {
      opacity: 1;
      visibility: visible;
      height: auto;
      flex-direction: row; }
      .navegacion a {
        font-size: 2rem;
        margin-right: 3rem; }
        .navegacion a:last-of-type {
          margin: 0; } }

